.friendList {
    display: block;
    margin-bottom: 50px;
  }
  
  .item {
    display: flex;
    align-items: center;
    margin-bottom: 5px;
    padding: 10px;
    border: 1px solid grey;
    width: 200px;
  }
  
  .span {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    margin-right: 10px;
  }
  
  .statusOnline {
    background-color: green;
  }
  
  .statusOffline {
    background-color: red;
  }
  
  .avatar {
    border-radius: 4px;
    margin-right: 10px;
  }